<template>
  <v-container fluid>
    <v-textarea
      name="input-7-1"
      filled
      label="markdown"
      auto-grow
      v-model="mmark"
    ></v-textarea>
    <h2>html</h2>
    <Markdown :mmark="mmark" />
    <v-btn @click="dbset">DBに登録</v-btn>
  </v-container>
</template>
<script>
import firebase from "firebase";
import Markdown from "../components/Markdown.vue";
export default {
  components: {
    Markdown
  },
  created() {
    this.writeAuth();
    this.id = Number(this.$route.hash.slice(1));
    this.db = firebase.database();
    let that = this;
    this.db
      .ref("recipe/md/" + this.id)
      .once("value")
      .then(snapshot => {
        if (snapshot.exists()) {
          that.mmark = snapshot.val().content;
        }
      });
  },
  data() {
    return {
      mmark: "",
      db: "",
      id: 0
    };
  },
  methods: {
    dbset() {
      let that = this;
      if (this.mmark.slice(-1) !== "\n") {
        this.mmark += "\n";
      }
      this.db
        .ref("recipe/md/" + this.id)
        .set({
          content: this.mmark,
          date: new Date().toDateString()
        })
        .then(() => {
          alert(`${this.id}に登録しました`);
          that.$router.push("home");
        });
    },

    writeAuth() {
      let that = this;
      this.db = firebase.database();
      this.db
        .ref("recipe/test")
        .set({})
        .then(
          () => {},
          () => {
            alert(
              "現在ログイン中のgoogleアカウントには書き込み権限がありません。"
            );
            that.$router.push("home");
          }
        );
    }
  }
};
</script>
